$c-light: #ffffff;
$c-dark: #5f458c;
$c-purple: #a67ee5;

$font: "Proxima Nova", "Arial", sans-serif;
$font-alt: "Radical",  "Arial", sans-serif;

$stop-scaling: 2000px;

$wide-screen: "min-aspect-ratio: 1680/650";

$wider: "min-aspect-ratio: 1441/760";
$narrower: "max-aspect-ratio: 1439/760";

$tablet: 1024px;
$mobile: 768px;

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

$index: 1.38871;

//Animations transition
$opacity-show-text-duration: 0.5s;
$bounce-in-animation: transform 0.4s cubic-bezier(0.4, 0.07, 0.29, 1.8);
$opacity-text-animation: opacity $opacity-show-text-duration ease;

//Story screen. Slider colors
$c-first-slide: (#a67ee5, #3e72ee, #a0ceff);
$c-second-slide: (#5f458c, #a0ceff, #3e72ee);
